<template>
  <div class="box">

    <ShowMenu
      class="show"
      :menu='menu'
      @add='add'
    ></ShowMenu>
    <Shopping
      class="shop"
      :shop='shop'
      @empty='empty'
      v-show="showShop"
    ></Shopping>
  </div>
</template>

<script>
import ShowMenu from "../components/ShowMenu";
import Shopping from "../components/Shopping";
export default {
  components: {
    ShowMenu,
    Shopping
  },
  data() {
    return {
      menu: {},
      shop: {},
      showShop: false
    };
  },
  methods: {
    fetchMenu() {
      this.$axios.get("/menu.json").then(res => {
        // console.log(res.data);
        this.$store.dispatch("asyncmenu", res.data);
        console.log(this.$store.getters.getmenu);

        this.menu = this.$store.getters.getmenu;
      });
      console.log("-------------" + this.menu);
    },
    update() {
      this.fetchMenu();
    },
    empty() {
      this.showShop = false;
    },
    add() {
      this.showShop = true;
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.fetchMenu();
    });
  }
};
</script>

<style scoped>
.box {
  width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}
.show {
  width: 60%;
}
.shop {
  width: 30%;
}
</style>