<template>
  <table class="table">
    <tbody>
      <tr>
        <th>数量</th>
        <th>品种</th>
        <th>价格</th>
      </tr>
    </tbody>
    <tbody>
      <tr
        v-for="(item,index) in shop"
        :key="index"
      >
        <td>
          <span @click="sub(index)">-</span>
          {{item.num}}
          <span @click="add(index)">+</span>
        </td>
        <td>{{item.type}}</td>
        <td>{{item.price * item.num}}</td>
      </tr>
    </tbody>
    <h4>总价：{{sum}}</h4>
    <button class="btn">购买</button>
  </table>

</template>

<script>
import { mapGetters } from "vuex";

export default {
  computed: {
    ...mapGetters(["shop"]),
    sum() {
      let s = 0;
      for (const k in this.shop) {
        if (this.shop.hasOwnProperty(k)) {
          const ele = this.shop[k];
          s = s + parseInt(ele.price * ele.num);
        }
      }
      return s;
    }
  },
  methods: {
    sub(index) {
      console.log("---");

      this.shop[index].num--;
      if (this.shop[index].num <= 0) {
        this.$store.dispatch("asyncDelete", this.shop[index].type);
      }
      if (JSON.stringify(this.shop) == "{}") {
        this.$emit("empty");
      }
    },
    add(index) {
      console.log("+++");

      this.shop[index].num++;
    }
  }
};
</script>

<style scoped>
td span {
  width: 20px;
  height: 20px;
  text-align: center;
  line-height: 20px;
  background-color: #ccc;
  border-radius: 5px;
  display: inline-block;
}
</style>