<template>
  <table class="table">
    <tbody>
      <tr>
        <th>尺寸</th>
        <th>价格</th>
        <th>加入</th>
      </tr>
    </tbody>
    <tbody
      class="containt"
      v-for="(item,index) in menu"
      :key='index'
    >
      <th>
      <td>{{item.name}}</td>
      </th>
      <tr
        v-for="(it,ind) in item.options"
        :key='ind'
      >
        <td>{{it.size}}</td>
        <td>{{it.price}}</td>
        <td><span @click='add(item.name,it.size,it.price)'>+</span></td>
      </tr>
    </tbody>

  </table>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  props: {
    menu: Object
  },
  computed: mapGetters(["shop"]),
  methods: {
    add(name, size, price) {
      this.$emit("add");
      let type = name + size;
      this.$store.dispatch("asyncshop", { type, num: 1, price });
      console.log(this.shop);
    }
  }
};
</script>

<style scoped>
span {
  border: 1px solid green;
  border-radius: 5px;
  text-align: center;
  width: 20px;
  height: 20px;
  line-height: 20px;
  color: green;
  font-size: 12px;
  display: inline-block;
}
</style>